html {
    background: #2a2a2e;
    color: #f9f9fa;
    user-select: none;
    overflow-x: hidden;
}

.search {
    width: calc(50% - 50px);
    border: solid 1px #f9f9fa33;
    background: #38383D;
    box-shadow: 0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.15), 0 1px 6px 4px rgb(42, 42, 46);
    font: inherit;
    color: inherit;
    border-radius: 3px;
    padding: 10px;
    margin: 10px auto;
    display: block;
    outline: none;
    position: sticky;
    top: 10px;
    transition: .2s ease;
    z-index: 1000;
}
.search:focus {
    box-shadow: 0 0 0 1px #45A1FF, 0 0 0 4px rgba(69, 161, 255, 0.3), 0 0 0 1px #45A1FF, 0 1px 6px 4px rgb(42, 42, 46);
}

.entries {
    display: flex;
    flex-flow: wrap;
    width: 100%;
    color: transparent;
    transition: .2s ease color;
}
.entries .entry {
    padding: 20px;
    background: #38383d;
    border-radius: 10px;
    margin: 10px;
    box-shadow: 0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.15), inset 0 0 0 1px #8c180f;
    cursor: pointer;
    transition: .2s ease, .2s .2s opacity, 0s background;
}
.entries .entry.selected {
    background: #404045;
    position: sticky;
    top: 10px;
}
.entries .entry.filtered {
    opacity: 0;
    margin: -150.5px;
    pointer-events: none;
    transition: .2s ease, .2s .2s margin;
}
.entries .entry#green {
    box-shadow: 0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.15), inset 0 0 0 1px #34a853;
}

.buttons {
    display: flex;
}
.buttons .button {
    background: #4A4A4F;
    padding: 10px;
    border-radius: 10px;
    margin: 10px;
    text-align: center;
    box-shadow: 0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.15);
    cursor: pointer;
    height: max-content;
    transition: .2s ease;
}
.buttons .button:hover {
    background: #56565C;
}

.about {
    padding: 15px 0;
    color: #f9f9fa6e;
    margin: -97px -97px 97px;
    position: relative;
    left: 97px;
    top: 97px;
}
.about p {
    margin: 0;
}

.right {
    width: 50%;
    right: 0;
    transition: .2s ease;
    position: absolute;
    top: 73px;
    margin: 0 40px 40px;
    display: none;
}
.right .title {
    margin-top: 0;
    font-size: 22px;
}
.right .add {
    position: absolute;
    right: 0;
    top: -13px;
    padding: 10px;
    cursor: pointer;
    background: #4a4a4f;
    border-radius: 10px;
    transition: .2s ease;
}
.right .add:hover {
    background: #56565C;
}
.right .wrapper {
    width: calc(100% - 15px);
    background: #38383d;
    padding: 15px;
    border-radius: 10px;
    position: relative;
    display: flex;
    align-content: center;
    flex-flow: column;
}
.right .wrapper .entry {
    position: relative;
    padding: 10px 100px 10px 10px;
    background: #4a4a4f;
    border-radius: 10px;
    margin: 10px 0;
}
.right .wrapper .entry .button {
    background: #34a853;
    padding: 10px;
    border-radius: 10px;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    transition: .2s ease;
}
.right .wrapper p {
    font-size: 20px;
    margin: 10px 0;
}
.right .wrapper .text {
    width: 50%;
    border: solid 1px #f9f9fa33;
    background: #38383D;
    font: inherit;
    color: inherit;
    border-radius: 3px;
    padding: 10px;
    margin: 10px auto;
    display: block;
    outline: none;
    transition: .2s ease;
    box-shadow: 0 0 0 1px #45A1FF, 0 0 0 4px rgba(69, 161, 255, 0.3), 0 0 0 1px #45A1FF, 0 1px 6px 4px rgb(42, 42, 46);
}
.right .wrapper .buttons {
    display: flex;
    margin: auto;
    width: max-content;
}
.right .wrapper .buttons .add {
    position: relative;
    margin: 0 5px;
    top: 10px;
}
.right .wrapper .buttons .add:first-of-type {
    opacity: .5;
}